.keyy_qrcode_container {
	text-align: center;
	padding-bottom: 12px;
}

.keyy_qrcode_container_disabled {
	text-align: center;
	font-style: italic;
	padding-bottom: 12px;
}

.keyy_qrcode_container .keyy_qrcode {
	clear: left;
}

.keyy_qrcode_descriptor {
	font-style: italic;
	clear: left;
	text-align: center;
	margin-top: 10px;
}

.keyy_qrcode_switch {
	clear: left;
	text-align: center;
	margin-top: 10px;
}

.tooltip {
	opacity: 0.8;
}

.ui-tooltip {
	padding: 8px;
	position: absolute;
	z-index: 9999;
	max-width: 300px;
	box-shadow: 0 0 5px #AAA;
}

body .ui-tooltip {
	border-width: 2px;
}

body.login #login span.keyy-switch-toggle {
	height: 13.5px;
}

.login .keyy-wave {
	padding-top: 20px;
	padding-bottom: 20px;
}

.keyy-debug {
	clear: left;
	overflow: scroll;
}

.login form {
	border: 20px solid #DB6939;
}

.keyy_wave_to_qrcode span {
	line-height: .9em;
	vertical-align: text-top;
}

.keyy-primary-header {
	font-size: 17px;
	margin-bottom: 25px;
	text-align: center;
	font-weight: 300;
	line-height: 1.6em;
}

.keyy-login-wrapper .keyy-login-logo {
	width: 100px;
	float: left;
	margin-bottom: 25px;
	box-shadow: none;
}

.keyy_qrcode_learn_more.button {
	clear: right;
	float: left;
	margin: 15px auto;
	width: 100%;
}

rect.anim {
	animation: groove 1s forwards infinite;
}

.keyy_qrcode.keyy_wave_container .keyy-thumbs-up {
	position: absolute;
	margin-top: 0px;
	padding: 0 20px;
}

.keyy-back {
	text-decoration: underline;
	margin-top: 10px;
	color: #DA521B;
	font-size: 16px;
	cursor: pointer;
}

img.keyy-app-store-image {
	margin: 3px;
	border-radius: 3px;
}

.keyy_qrcode_wp_login_header {
	text-align: left;
	font-size: 12px;
	font-style: normal;
	margin-top: 20px;
}

.keyy_qrcode_container .wave-label,
.keyy_qrcode_container .qr-label {
	cursor: pointer;
}

.keyy-login-secondary-header {
	text-align: center;
	clear: both;
	font-size: 21px;
	font-weight: 300;
	margin-bottom: 20px;
}

img.keyy-intro-gif {
	max-width: 300px;
	margin: 0 28px;
}

h1.keyy-login-learn-more-header {
	font-weight: 300;
	line-height: 1;
	margin-bottom: 10px;
}

h4.keyy-learn-more-text-header {
	text-align: center;
	margin-bottom: 20px;
	font-weight: 300;
	font-size: 20px;
}

span.keyy-switch-text-label {
	line-height: .8em;
	vertical-align: text-top;
}

span.keyy_learn_more {
	color: #DB6939;
	text-decoration: underline;
	cursor: pointer;
}

ul.keyy-learn-more-text-list {
	margin: 10px;
	font-size: 14px;
}

input.keyy-back.button {
	margin: 20px 0 0 0;
}

.keyy-back-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-pack: center;
	justify-content: center;
}

/** Login modal fix **/
#wp-auth-check-wrap #wp-auth-check {
	min-width: 500px;
}

/** Need more specificity here as WP overides defaults */
input.keyy_qrcode_learn_more.button.button-primary.button-large {
	margin-bottom: 25px;
}

/** Prevents a jump in the login page when switching */
.keyy_qrcode.keyy_wave_container {
	min-height: 299px;
}

.keyy_qrcode.keyy_wave_container img {
	margin-top: 55px;
	margin-left: auto;
	margin-right: auto;
	box-shadow: none;
}

.login form {
	margin-top: 20px;
	margin-left: 0;
	padding: 26px 24px;
}

.keyy_qrcode svg:not(:root) {
	overflow: visible;
	margin-top: 15px;
}

/** The Keyy wave dance */
.keyy-wave > rect {
	display: none;
}

@keyframes groove {

	0% {
		transform: translatey(-45px);
}

	50% {
		transform: translatey(45px);
}

	100% {
		transform: translatey(-45px);
}

}

@keyframes encircle {

	from {
		stroke-dashoffset: 1326;
	}

	to {
		stroke-dashoffset: 0;
	}

}

@keyframes rotateOut {

	0% {
		transform-origin: center center;
		transform: rotatey(0);
		opacity: 1;
	}

	100% {
		transform-origin: center center;
		transform: rotatey(90deg);
		opacity: 0;
	}

}

@keyframes rotateIn {

	0% {
		transform-origin: center center;
		transform: rotatey(90deg);
		opacity: 0;
	}

	100% {
		transform-origin: center center;
		transform: rotatey(0);
		opacity: 1;
	}

}

/** Theme related spacing fix */
.widget object.keyy-thumbs-up {
	left: 20px;
}

/** Pure CSS toggle, please test your calculations before altering dimensions */
.keyy-login-wrapper .keyy-switch {
	position: relative;
	width: 40px;
	margin: 0 10px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	display: inline-block;
}

.keyy-login-wrapper .keyy-switch-checkbox {
	display: none;
}

.keyy-login-wrapper .keyy-switch-label {
	display: block;
	overflow: hidden;
	cursor: pointer;
	border: 2px solid #DB6939;
	border-radius: 20px;
}

.keyy-login-wrapper p.keyy_qrcode_learn_more {
	text-decoration: underline;
	font-style: italic;
	margin-top: 10px;
	color: #DA521B;
	cursor: pointer;
}

.keyy-login-wrapper .keyy-switch-inner {
	display: block;
	width: 200%;
	margin-left: -100%;
	transition: margin 0.3s ease-in 0s;
}

.keyy-login-wrapper .keyy-switch-inner:before,
.keyy-login-wrapper .keyy-switch-inner:after {
	display: block;
	float: left;
	width: 50%;
	height: 14px;
	padding: 0;
	line-height: 14px;
	font-size: 14px;
	color: white;
	font-family: trebuchet, arial, sans-serif;
	font-weight: bold;
	box-sizing: border-box;
}

.keyy-login-wrapper .keyy-switch-inner:before {
	content: "";
	padding-left: 10px;
	background-color: #DB6939;
	color: #FFF;
}

.keyy-login-wrapper .keyy-switch-inner:after {
	content: "";
	padding-right: 10px;
	background-color: #EEE;
	color: #999;
	text-align: right;
}

.keyy-login-wrapper .keyy-app-icon-container {
	width: 100%;
	text-align: center;
	margin: 15px 0;
}

.keyy-login-wrapper .keyy-switch-toggle {
	display: block;
	width: 15px;
	margin: 1px 0;
	height: 16px;
	background: #FFF;
	position: absolute;
	top: 0;
	bottom: 0;
	right: 22px;
	border: 2px solid #999;
	border-radius: 20px;
	transition: all 0.3s ease-in 0s;
}

body.login #login .keyy-login-wrapper span.keyy-switch-toggle {
	height: 12.5px;
}

.keyy-login-wrapper .keyy-switch-checkbox:checked + .keyy-switch-label .keyy-switch-inner {
	margin-left: 0;
}

.keyy-login-wrapper .keyy-switch-checkbox:checked + .keyy-switch-label .keyy-switch-toggle {
	right: 0px;
}

.keyy-login-wrapper input.keyy-switch-checkbox {
	display: none;
}